import React from 'react'
import {
    StaticQuery,
    graphql,
    Link,
} from 'gatsby'

import styles from './Header.module.scss'

import Menu from './Menu'

const Header = () => (
    <StaticQuery
        query={graphql`
        query {
            notificationsImg: file(relativePath: { eq: "notifications.svg" }) {
            publicURL
            }
            logo: file(relativePath: { eq: "logo-text.svg" }) {
            publicURL
            }
        }
        `}
        render={data => (
            <header className={styles.header}>
                <div className={styles.header__container}>
                    <Menu />
                    <div className={styles.main}>

                        <div className={styles.info}>
                            <div className={styles.info__title}>Активы:</div>
                            <div className={styles.info__text}>
                    1 234 456 руб.
                                <hr />
                    19 041$
                                <hr />
                    56%
                            </div>
                        </div>
                        <div className={styles.info}>
                            <div className={styles.info__title}>Свободные средства:</div>
                            <div className={styles.info__text}>
                    1 234 456 руб.
                                <hr />
                    19 041$
                                <hr />
                    56%
                            </div>
                        </div>
                        <div className={styles.info}>
                            <div className={styles.info__title}>Всего:</div>
                            <div className={styles.info__text}>
                    1 234 456 руб.
                                <hr />
                    19 041$
                                <hr />
                    56%
                            </div>
                        </div>

                        <div className={styles.notifications}>
                            <img src={data.notificationsImg.publicURL} alt="notifications" />
                        </div>

                        <div className={styles.logo}>
                            <Link to="/">
                                <img src={data.logo.publicURL} alt="logo" />
                            </Link>
                        </div>
                    </div>

                </div>
            </header>
        )}
    />
)

export default Header
