import React from 'react'
import styles from './Layout.module.scss'

import Header from '../Header'
import Aside from '../Aside'

const Layout = ({ children }) => (
    <React.Fragment>
        <Header />
        <div className={styles.container}>
            <Aside />
            <main>{children}</main>
        </div>
    </React.Fragment>
)

export default Layout
